<template>
  <div class="login">
    <div class="title">管理系统登录</div>
    <div class="form-card">
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item
          label="用户名"
          prop="username"
          :rules="[
            { required: true, message: '用户名不能为空', trigger: 'blur' },
            { min: 4, max: 10, message: '4-10', trigger: 'blur' },
          ]"
        >
          <el-input v-model="form.username"></el-input>
        </el-form-item>
        <el-form-item
          label="密码"
          prop="password"
          :rules="[
            { required: true, message: '密码不能为空', trigger: 'blur' },
            { min: 6, max: 12, message: '6-12', trigger: 'blur' },
          ]"
        >
          <el-input type="password" v-model="form.password"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button size="medium" type="primary" @click="login"
            >登录</el-button
          >
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
const f = performance.getEntriesByType("navigation");
console.log(f);
export default {
  name: "Login",
  data: () => {
    return {
      form: {
        username: "",
        password: "",
      },
    };
  },
  props: {},
  methods: {
    async login() {
      const vaild = await this.$refs.form.validate();
      if (!vaild) return;
      const res = await fetch("http://1.116.64.64:5004/api2/login", {
        method: "POST",
        body: JSON.stringify(this.form),
      });
      console.log(res);
    },
  },
  beforeCreate() {
    console.log("beforeCreate");
  },
  created() {
    console.log("created");
  },
  beforeMount() {
    console.log("beforeMount");
  },
  beforeUpdate() {
    console.log("beforeUpdate");
  },
  mounted() {
    console.log("mounted");
  },
  updated() {
    console.log("updated");
  },
};
</script>

<style lang="less" scoped>
.login {
  width: 50%;
  height: 600px;
  background-color: rgb(60, 216, 86);
  margin: 200px auto;
  text-align: center;
  color: #fff;
  .title {
    padding-top: 20px;
    font-size: 32px;
  }
  .form-card {
    padding: 100px 10%;

    :deep(.el-button) {
      line-height: 1.5;
      font-size: 18px;
      padding: 10px 48px;
    }
  }
}
</style>
